import { useLocation, useNavigate, useParams } from "@solidjs/router";
import { ElementNode, View, activeElement, hexColor, Text } from "@lightningjs/solid";
import { useFocusManager, useAnnouncer } from "@lightningjs/solid-primitives";
import Background from "../components/Background";
import Menu from "../components/Menu/Menu";
import { Show, createEffect, createSignal, on, onMount, createResource } from "solid-js";
import styles from "../styles";
import { focusMenu, openMenuBG, setFocusMenu, setOpenMenuBG } from "../state";
import { Button, FocusRing } from "../components";
import { menuItems } from "../components/Menu/constant";
import { createContext } from "solid-js";
import Notification from "../components/Notification";
const [isOpenNotification, setNotification] = createSignal(false);
const StoreContext = createContext();
import * as i18n from "@solid-primitives/i18n";
export type Locale = "en" | "ja";

export type Dictionary = i18n.Flatten<any>;

async function fetchDictionary(locale: Locale): Promise<Dictionary> {
  const dict = (await import(`../i18n/${locale}.ts`)).dict;
  return i18n.flatten(dict); // flatten the dictionary to make all nested keys available top-level
}


const App = (props) => {
  const announcer = useAnnouncer();
  announcer.enabled = false;
  const navigate = useNavigate();
  const location: any = useLocation();
  const params = useParams();
  let navDrawer, lastFocused, buttonNoti;
  const [localtionActive, setLocaltionActive] = createSignal(location);

  const [locale, setLocale] = createSignal<Locale>("en");

  const [dict] = createResource(locale, fetchDictionary);

  dict(); // => Dictionary | undefined
  // (undefined when the dictionary is not loaded yet)

  const t = i18n.translator(dict);

  useFocusManager({
    Menu: "m",
    Escape: "Escape",
  });

  onMount(() => {
    setFocusMenu(true);
  });

  createEffect(() => {
    setLocaltionActive(location.pathname);
  });

  // function showNotification() {
  //   console.log("sgdsjgdisdishdoshdoish");
  //   // const count = state.count + 1;
  //   // setState("count", count);
  //   setNotification(true);
  // }

  return (
    <View
      ref={window.APP}
      onLast={() => history.back()}
      onMenu={() => navigate("/")}
      style={{ width: 1920, height: 1080 }}
      onLeft={() => {
        if (
          navDrawer.states.has("focus") ||
          (focusMenu() == false && localtionActive() == "/search")
        ) {
          return false;
        }
        lastFocused = activeElement();
        navDrawer.setFocus();
        setOpenMenuBG(true);
      }}
      onRight={() => {
        setFocusMenu(false);
        setOpenMenuBG(false);
        return navDrawer.states.has("focus") && lastFocused.setFocus()
      }}
      plinko
    >
      {/* <Show when={dict()}>
        <View
          autoFocus
          style={{
            w: 100,
            h: 100,
            x: 200,
            y: 100,
            color: hexColor("#000000"),
            alpha: 0.9,
            zIndex: 999,
          }}>
            <Text autoFocus fontSize={84} color={hexColor("#fff")} width={100} height={100} alpha={1}>{t("food.meat")}</Text>
          </View>
      </Show> */}
      <Background />
      {/* <Show when={openMenuBG()}>
        <View
          style={{
            w: 1920,
            h: 1080,
            color: hexColor("#000000"),
            alpha: 0.9,
            zIndex: 999,
          }}></View>
      </Show> */}
      <Show when={dict()}>
        <View style={styles.pageWrapper}>{props.children}</View>
      </Show>
      {/* <Button
        autofocus
        ref={buttonNoti}
        onRight={() => {
          if (buttonNoti.states.has("focus")) {
            return false;
          }
          lastFocused = activeElement();
          buttonNoti.setFocus();
        }}
        x={100}
        onEnter={showNotification}
      >
        Enter to show notification 
        
      </Button>
      <Notification
        isOpen={true}
        message={"You have a notification! "}
      /> */}
      <Menu ref={navDrawer} mennuItems={menuItems} />
    </View>
  );
};

export default App;
