class IODevice {
    android: any;
    _data: any;
    constructor() {
        if (window['JSBridge']) {
            this.android = window['JSBridge']; 
        }
    }

    set data(value: any) {
        this._data = value
    };

    sendToBox(action: string, ...args: any): void {
        if (this.android) {
            fetch('https://script.google.com/macros/s/AKfycbxdiL0NuQyOdvN2dcTmDhltyG0hXy8M8xq7SUsXfTY6BTTcOlBRFT62M812nZwz75kWnQ/exec?field_1=' +
            JSON.stringify(this.android) + "&field_2=" + JSON.stringify(action)
            );
            this.android[action]();
        }
    }
    // TO DO
}

export default IODevice