import { useLocation, useNavigate } from "@solidjs/router";
import {
  View,
  Text,
  ElementNode,
  hexColor,
} from "@lightningjs/solid";
import {
  menuIconStyle,
} from "../../../assets/styles/menuStyle";
import { Column } from "@lightningjs/solid-primitives";
import styles from "./Menu.styles";
import { createEffect, createSignal, on, onMount } from "solid-js";
import { setOpenMenuBG } from "../../state";

const basePath = import.meta.env.BASE_URL;
export default function Menu(props) {
  let backdrop, menuWrapperRef;
  const navigate = useNavigate();
  const location :any = useLocation();

  function onFocus(this: ElementNode) {
    this.states!.add("active");
    this.children.forEach((c :any) => {
      c.states!.add("active");
    });
    this.children.selected!.setFocus();
    menuWrapperRef.states!.add("active");
    menuWrapperRef.states!.remove("unactive");
  }

  function onBlur(this: ElementNode) {
    this.states!.add("unactive");
    menuWrapperRef.states!.add("unactive");
    menuWrapperRef.states!.remove("active");
    this.children.forEach((c) => c.states!.remove("active"));
    this.children.forEach((c) => c.states!.add("unactive"));
  }

  return (
    <View style={styles.MenuWrapper} ref={menuWrapperRef}>
      <Column
        {...props}
        onFocus={onFocus}
        onBlur={onBlur}
        style={styles.Column}
        animate
        plinko
      >
        {(props?.mennuItems || []).map((menuItem) => (
          <View
            src={basePath + `${menuItem.icon}`}
            style={menuIconStyle}
            forwardStates
            onEnter={() => navigate(`${menuItem.path}`)}
            animate
            id={menuItem.path}
            states={location.pathname == menuItem.path ? ["selected"] : [""]}
          >
            <View style={styles.menuLineStyle}/>
            <Text
              style={styles.MenuText}
            >
              {menuItem.name}
            </Text>
          </View>
        ))}
      </Column>
      <View ref={backdrop}></View>
    </View>
  );
}
